import React from 'react'

export const Footer = (props) => {
    let footer = <div className='bg-dark position-relative w-100 pb-3 pt-1 text-light  text-center'>Copyright &copy; MyToDos.com  </div>
    return (
        <div>
            {props.visible ? footer : "Footer not available"}
        </div>
    )
}
