import React from 'react'
import { useState } from 'react';
//import heartImage from './love-hearts.png'
import heartImage from './like-icon.png'
// import heartImage from './Ballons.png'

export const AddToDo = (props) => {

    // const [title, setTitle] = useState('Order Biryani');
    // const [description, setDescription] = useState('Call Chicken Point and order Hyderabadi Roasted Biryani');

    const [title, setTitle] = useState('');
    const [description, setDescription] = useState('');

    const [isAnimationReady, setIsAnimationReady] = useState(true);

    let loveView = <img id='loveImage' src={heartImage} alt=" not found" className='d-none height float-start' />

    //let loveView='abc'
    let addToDoView =

        <form id='addToDoForm' className=' mx-auto my-3 card shadow rounded rounded-3 bg-transparent text-center my-auto postion-absolute'>
            {loveView}
            <div id='loveContainer' className='d-none height float-start'></div>
            <div className='card-header'>
                <input type="text" className="h4 lead text-center text-capitalize fw-bold border-0  bg-transparent" id="title" placeholder="Enter Title" value={title} onChange={(e) => { setTitle(e.target.value) }} />
            </div>

            <div className="card-body p-2  ">
                <textarea type="textarea" className="col-12 bg-transparent lead text-center border-0" id="description" placeholder="Enter Description" value={description} onChange={(e) => { setDescription(e.target.value) }} />

            </div>
            <div className="card-footer p-2  ">
                <button className='btn btn-sm btn-outline-primary mx-auto' type='submit' onClick={(e) => {
                    e.preventDefault();
                    if (title === 'Mahima' && !description) {
                        if (isAnimationReady) {
                            setIsAnimationReady(false);
                            document.getElementById('loveImage').classList.remove('d-none');
                            document.getElementById('loveImage').classList.add('animatedLove');
                            document.getElementById('addToDoForm').classList.add('addToDoForm');
                            setTimeout(() => {
                                document.getElementById('loveImage').classList.add('d-none');
                                document.getElementById('loveImage').classList.remove('animatedLove');
                                document.getElementById('addToDoForm').classList.remove('addToDoForm');
                                setIsAnimationReady(true);
                            }, 5000);
                         } else { }
                        

                    } else
                        if (!title || !description) {
                            alert('Enter Title and Description both')

                        }
                        else {
                            props.onAdd({ title: title, description: description });
                            setTitle('');
                            setDescription('');
                        }
                }}>Add To Do</button>

            </div>

        </form>


    return (addToDoView)
}
