import React from 'react'

export const ToDoItems = (props) => {
    let toDoView =
        <div className=' mx-auto my-3 card shadow rounded rounded-3 bg-transparent text-dark col-9 col-lg-4 text-center'>
            <div className='h4 lead text-capitalize fw-bold card-header'>{props.todoItem.title}</div>

            <div className="card-body   ">
                <div className='lead mb-3'>{props.todoItem.description}</div>
            </div>
            <div className="card-footer  ">
                <button className='btn btn-sm btn-outline-primary mx-auto' onClick={() => props.onDelete(props.todoItem)}>Done</button>

            </div>

        </div>

    return (toDoView)
}
